.hero {
  height: 421px;
  background: url(../images/hero-image-mobile.jpg) center bottom no-repeat;
  background-size: cover;
  z-index: -2;
}

.hero .claim {
  width: 290px;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: flex-end;
}

.hero .claim .hero-title {
  width: 260px;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 14px;
  padding-bottom: 16px;
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 1.39;
  color: #000000;
  display: inline-block;
  background-color: white;
  transform: translateY(50%);
  flex-grow: 1;
}

.about-us {
  width: 90.6%;
  margin: auto;
  margin-top: 46px;
  background-color: white;
  justify-content: center;
  z-index: 0;
}

.about-us .title {
  padding-top: 30px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 0;
  margin-bottom: 15px;
  font-family: 'Rubik', sans-serif;
  font-size: 24px;
  font-weight: 500;
  line-height: 45px;
  color: #14d9c4;
  text-align: center;
  display: block;
}

.about-us .team {
  width: 100%;
}

.about-us .team p {
  padding-top: 0;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #54585a;
  text-align: center;
}

.about-us .team p:last-child {
  margin-bottom: 2px;
}

.second-section {
  padding-bottom: 45px;
  background-color: rgba(184, 184, 185, 0.2);
  display: block;
}

.second-section .second {
  width: 100%;
  margin-top: 2px;
  margin-left: 15px;
  margin-right: 15px;
}

.second-section .second p {
  margin-top: 0;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  line-height: 1.5;
  font-weight: normal;
  color: #54585a;
  text-align: center;
}

.second-section .second p:last-child {
  margin-bottom: 0;
}

.second-section .second .strengths {
  height: 45px;
  padding-top: 30px;
  margin-top: 0;
  margin-bottom: 0;
  opacity: 100%;
  font-size: 22px;
  font-family: 'Rubik';
  font-weight: 500;
  line-height: 2.05;
  color: #099d8d;
  text-align: center;
  text-transform: none;
}

.second-section .second .weaknesses {
  height: 45px;
  padding-top: 30px;
  margin-top: 0;
  margin-bottom: 0;
  opacity: 100%;
  font-family: 'Rubik';
  font-size: 22px;
  font-weight: 500;
  line-height: 2.05;
  color: #099d8d;
  text-align: center;
  text-transform: none;
}

.persons {
  width: 100%;
  margin-top: 30px;
  padding-bottom: 45px;
}

.persons h2 {
  margin-bottom: 0%;
  font-family: 'Rubik', sans-serif;
  font-size: 24px;
  line-height: 45px;
  font-weight: 500;
  text-transform: uppercase;
  color: #099d8d;
  text-align: center;
}

.persons .presentation {
  width: 90.6%;
  margin-left: auto;
  margin-right: auto;
}

.persons .presentation .name {
  margin-top: 10px;
  margin-bottom: 0%;
  font-family: 'Open Sans', sans-serif;
  font-size: 20px;
  font-weight: 500;
  line-height: 2.25;
  color: #000000;
  text-align: center;
}

.persons .presentation .description {
  padding-bottom: 15px;
  margin-top: 0;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  font-weight: normal;
  line-height: 1.5;
  color: #54585a;
  text-align: center;
}

.persons .presentation .photo {
  height: 170px;
  width: 170px;
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
  border: 5px solid rgba(0, 0, 0, 0.08);
  border-radius: 10px;
  text-align: center;
  display: block;
}

.persons .presentation .photo.first-photo {
  margin-top: 25px;
}

.persons .presentation .social {
  width: 124px;
  height: 30px;
  margin-left: auto;
  margin-right: auto;
  color: #54585a;
  display: flex;
  justify-content: space-between;
}

.persons .presentation .social .fab {
  width: 16px;
  height: 16px;
  color: #099d8d;
}

.persons .presentation .social .fas {
  color: #099d8d;
}

.persons .presentation .social .point {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  color: #54585a;
}

.square {
  height: 30px;
  width: 30px;
  margin-left: auto;
  margin-right: auto;
  background-color: white;
  position: relative;
  top: 14.2px;
  transform: rotate(45deg);
}

.header-content {
  width: 100%;
  position: fixed;
  top: -15px;
  justify-content: center;
  z-index: 2;
}

.header-content .tittle-header {
  height: 30px;
  padding-top: 15px;
  padding-bottom: 15px;
  margin-left: auto;
  margin-right: auto;
  background-color: #099d8d;
  opacity: 0.95;
  text-align: center;
}

.header-content .tittle-header h1 {
  height: 30px;
  margin-top: 8px;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  font-family: 'Rubik', sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.88;
  text-transform: uppercase;
  color: #ffffff;
}

.header-content .first-navigation {
  height: 45px;
  background-color: #14d9c4;
  opacity: 0.95;
  text-align: center;
  display: flex;
  justify-content: space-evenly;
}

.header-content .first-navigation a {
  height: 30px;
  padding-top: 8px;
  padding-bottom: 7px;
  font-family: 'Rubik', sans-serif;
  font-size: 15px;
  font-weight: 500;
  line-height: 2;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
  display: inline-block;
  vertical-align: center;
}

.footer-content {
  width: 100%;
  background-color: white;
  border-top: 1px solid #b8b8b9;
  text-align: center;
}

.footer-content .copyright {
  height: 20px;
  padding-top: 30px;
  padding-bottom: 20px;
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  line-height: 1.43;
  color: #b8b8b9;
  text-align: center;
  display: inline-block;
}

.footer-content .footer-navigation {
  padding-bottom: 30px;
  margin: auto;
  opacity: 0.75;
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.43;
  color: #54585a;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.footer-content .footer-navigation a {
  color: #54585a;
  opacity: 0.75;
}

.footer-content .footer-navigation a:hover {
  color: #14d9c4;
  opacity: 0.75;
}

.footer-content .logo {
  width: 115.9px;
  height: 45px;
  padding-bottom: 45px;
}

.wrapper {
  width: 100%;
  height: 100%;
  padding-top: 0;
  border: 1px solid #b8b8b9;
  box-sizing: border-box;
  justify-content: center;
}

.wrapper .main-content {
  padding-top: 0;
  margin-top: 0;
  display: block;
}

.wrapper {
  width: 100%;
  height: 100vh;
  background-color: white;
  text-align: left;
}

.wrapper .main__contact {
  width: 100%;
  text-align: center;
  margin: 0;
  font-weight: 500;
  font-stretch: normal;
  line-height: 1.88;
  letter-spacing: normal;
  color: #54585a;
}

.wrapper .main__contact.title {
  font-size: 24px;
  font-family: 'Rubik', Arial, sans-serif;
  color: #099d8d;
  width: 127px;
  height: 45px;
  padding: 0;
  margin: auto;
}

.wrapper .main__contact.text {
  font-family: 'OpenSans', Arial, sans-serif;
}

.wrapper .main__contact.text.p {
  font-size: 16px;
  margin-top: 15px;
  height: 122px;
}

.wrapper .main__contact.text.small {
  font-size: 12px;
  height: 22px;
}

@media all and (min-width: 768px) {
  .hero {
    height: 421px;
    background: url(../images/hero-image-tablet-web.jpg) center bottom no-repeat;
    background-size: cover;
  }

  .hero .claim {
    width: 441px;
    margin-left: 45px;
    margin-right: 0;
    opacity: 0.8;
  }

  .hero .claim .hero-title {
    width: 100%;
    padding-left: 30px;
    padding-right: 30px;
    background-color: rgba(255, 255, 255, 0.8);
    display: inline-block;
    transform: translateY(0);
  }

  .about-us {
    margin-bottom: 30px;
    margin-top: 45px;
  }

  .about-us .title {
    padding-top: 0;
    margin-top: 0;
    margin-bottom: 0;
    color: #099d8d;
  }

  .about-us .team {
    opacity: 0.67;
  }

  .second-section {
    padding-right: 30px;
    padding-left: 30px;
    padding-bottom: 60px;
    display: flex;
  }

  .second-section .second {
    width: 50%;
  }

  .second-section .second .strengths {
    padding-top: 0;
    padding-bottom: 15px;
    margin-top: 45px;
  }

  .second-section .second .weaknesses {
    padding-top: 0;
    padding-bottom: 15px;
    margin-top: 45px;
  }

  .persons {
    width: 89%;
    padding-bottom: 0;
    margin-left: auto;
    margin-right: auto;
  }

  .persons h2 {
    margin-top: 45px;
    margin-bottom: 36px;
  }

  .persons .presentation {
    width: 100%;
    margin-bottom: 55px;
    display: flex;
    align-self: baseline;
  }

  .persons .presentation.left {
    display: flex;
    flex-direction: row;
  }

  .persons .presentation.left .name {
    text-align: left;
    margin-left: 26px;
  }

  .persons .presentation.left .description {
    margin-left: 26px;
    text-align: left;
  }

  .persons .presentation.right {
    display: flex;
    flex-direction: row-reverse;
  }

  .persons .presentation.right .name {
    margin-right: 26px;
    text-align: right;
  }

  .persons .presentation.right .description {
    margin-right: 26px;
    text-align: right;
  }

  .persons .presentation .name {
    margin-top: 0;
  }

  .persons .presentation .description {
    margin-bottom: 0;
  }

  .persons .presentation .photo.first-photo {
    margin-top: 0;
  }

  .persons .presentation .photo {
    width: 195px;
    height: 195px;
    margin-top: 0;
  }

  .persons .presentation .social.no-reverse {
    margin-left: 26px;
  }

  .persons .presentation .social.reverse {
    margin-right: 26px;
  }

  .header-content {
    display: flex;
    justify-content: flex-start;
  }

  .header-content .tittle-header {
    width: 50vw;
    padding-left: 45px;
    margin: 0;
    text-align: left;
  }

  .header-content .tittle-header h1 {
    padding-top: 9px;
    margin-top: 0;
  }

  .header-content .first-navigation {
    width: 50vw;
    height: 30px;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-right: 45px;
    margin-top: 0;
    background-color: #099d8d;
    opacity: 0.95;
    text-align: right;
    justify-content: flex-end;
  }

  .header-content .first-navigation a {
    margin-left: 18px;
    display: inline-block;
  }

  .footer-content {
    width: 100%;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .footer-content .copyright {
    width: 20%;
    padding-top: 30px;
    margin-left: 45px;
    order: 1;
  }

  .footer-content .footer-navigation {
    width: 20%;
    padding-top: 30px;
    margin-right: 45px;
    line-height: 2.14;
    text-align: end;
    order: 2;
  }

  .footer-content .logo {
    padding-bottom: 60px;
    margin-left: 45px;
    position: absolute;
    top: 65px;
    text-align: start;
  }
}

